.game-board-section {
    border: 1px solid #000;
    font-size: 36px;
    font-weight: bold;
    line-height: 34px;
    height: 20px;
    text-align: center;
    width: 20px;
    display: table;
}

.game-board-row:after {
    clear: both;
    content: "";
    display: table;
}

.gameBoard {
    display: flex;
    flex-direction: row;
    position: relative;
    margin-top: 4%;
    margin-left: 20%;
    max-width: 40%;
}