

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
    font-weight: 1000;
    color: white;
    text-align: center;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: right;
    font-size: 36px;
    font-weight: bold;
    line-height: 34px;
    height: 60px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 60px;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game-board {
    display: flex;
    flex-direction: row;
    position: absolute;
    margin-top: 10%;
    margin-left: 40%;
}


.game-info {
    margin-left: 20px;
}

#reset {
    background-color: #3500D3;
	padding: 5px 10px;
	font-size: large;
    color: white;
    margin-left: 25%;
}
