/*
****COLOUR SCHEME****
Dark - 0C0032
Grey - 282828
Dark Purple - 190061
Mid Purple - 240090
Lightish Purple - 3500D3
*/

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.navbar {
	background-image: linear-gradient(to right, #240090, #3500D3);
	top: 0px;
	left: 0px;
	width: 100%;
}

  
.dropdown {
	position: relative;
	display: inline-block;
}
  
.dropdown-content {
	display: none;
	position: absolute;
	background-color: #f1f1f1;
	min-width: 160px;
	box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}
  
.dropdown-content a {
	color: black;
	padding: 12px 16px;
	text-decoration: none;
	display: block;
	z-index: 999;
}
  
.dropdown-content a:hover {
	background-color: #ddd;
	z-index: 999;

}

.dropdown:hover .dropdown-content {
	display: block;
	z-index: 999;

}
  
.dropdown:hover .dropbtn {background-color: #3e8e41;}

.navbutton{
	background-color: #3500D3;
	padding: 25px 50px;
	font-size: large;
	color: white;
}

.rightProjects {
	top: 0px;
	right: 0px;
	position: absolute;
}

.background {
	background-image: url("./images/webBG.jpg");
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
	width: 100%;
	height: 100%;
	position: fixed;
	z-index: 1000;
	top: 0px;
    left: 0px;
}

.center {
	position: absolute;
	margin: 20% 30%;
}

.instructions {
	font-weight: 1000;
}

.mainBox {
	margin: 20% 25%;
	width: 40%;
	height: 25%;
	text-align: center;
	padding: 10px;
	border: 3px solid #240090;
	background-color: #3500D3;
	z-index: 1;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
