.boardSegment {
    border: 1px solid #000;
    font-size: 36px;
    font-weight: bold;
    line-height: 34px;
    height: 20px;

    text-align: center;
    width: 20px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.board {
    display: flex;
    flex-direction: row;
    position: relative;
    margin-top: 4%;
    margin-left: 20%;
    max-width: 40%;
}

.score {
    position: absolute;
    margin-top: 100%;
    margin-left: 60%;
    color: #fff;
    font-size: 36px;
    font-weight: bold;
}