.mazeSquare {
    border: 1px solid #999;
    font-size: 36px;
    font-weight: bold;
    line-height: 34px;
    height: 50px;
    width: 50px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.board {
    display: flex;
    flex-direction: row;
    position: relative;
    margin-top: 4%;
    margin-left: 25%;
    max-width: 40%;
}

.wInput {
    font-size: 36px;
    font-weight: bold;
    line-height: 34px;
    position: absolute;
    text-align: center;
    margin-left: 7%;
    margin-top: 20%;
}
