.chessSquare {
    border: 1px solid #999;
    font-size: 36px;
    font-weight: bold;
    line-height: 34px;
    height: 70px;
    width: 70px;
    display: table;
}

.chessRow:after {
    clear: both;
    content: "";
    display: table;
}

.game {
    display: flex;
    flex-direction: row;
    position: relative;
    margin-top: 4%;
    margin-left: 25%;
    width: 15%;
}


