.maze-solver-section {
    border: 1px solid #999;
    font-size: 36px;
    font-weight: bold;
    line-height: 34px;
    height: 10px;
    width: 10px;
    display: table;
}

.solver-board-row:after {
    clear: both;
    content: "";
    display: table;
}

.board {
    display: flex;
    flex-direction: row;
    position: relative;
    margin-top: 4%;
    margin-left: 20%;
    width: 15%;
}

.option {
    border: 1px solid #999;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 50px;
    width: 100px;
}

