.squareSudoku {
    background: #fff;
    border: 1px solid #999;
    font-size: 36px;
    font-weight: bold;
    line-height: 34px;
    height: 50px;
    display: table;
    text-align: center;
    width: 50px;
}

.board-row-sudoku:after {
    clear: both;
    content: "";
    display: table;
}

.options {
    float: left;
    margin-left: 20%;
}

#delete {
    padding: 10px 20px;
    font-weight: bold;
    margin-left: 5%;
    background-color: #fff;
}

.sudokuBoard {
    display: flex;
    flex-direction: row;
    position: relative;
    margin-top: 4%;
    margin-left: 30%;
    width: 20%;

}

.btn {
    background-color: #3500D3;
	padding: 10px 20px;
	font-size: large;
    color: white;
    position: absolute;
	margin-top: 160%
}

#resetSudoku { margin-left: 40%;}
#solve { margin-left: 70%;}